import Category from "../category/Category";
import Search from "../search/Search";

const FilterForm = ({onFilterSelect, filter, onUpdateSearch}) => {
    return (
        <form className="filter-form" action="#">
            <div className="row">
                <Category filter={filter} onFilterSelect={onFilterSelect}/>
                <Search onUpdateSearch={onUpdateSearch}/>
            </div>
        </form> 
    );
}

export default FilterForm;