const Category = ({onFilterSelect, filter}) => {
    let buttonsData = [
        {value: 'all'},
        {value: 'africa'},
        {value: 'south america'},
        {value: 'asia pacific'},
        {value: 'central america'}
    ];

     
    let buttons = buttonsData.map(({value}) => {
        let active = false;
        if (filter === value) {
            active = true;
        }
        let useClass = active ? 'active' : '';
        return (
            <input className={useClass} type="button" key={value} value={value} onClick={() => onFilterSelect(value)}/>
        )
    })

    return (
        <div className="category-block">
            <div className="category-group">
                {buttons}
            </div>
        </div>
    );
}

export default Category;