import { Helmet } from "react-helmet";
import { useEffect, useState } from 'react';

import Header from "../header/Header";
import Footer from "../footer/Footer";
import Intro from "../intro/Intro";
import MainInfo from "../mainInfo/MainInfo";
import FilterForm from "../filter/Filter";
import Products from "../products/Products";

import useSearchProducts from "../../utils/useSearchProducts";
import setContent from "../../utils/setContent";

import bgImg from "../../images/bg-img-02.jpg";
import mainImg from "../../images/img-02.jpg";

const OurCoffeePage = () => {
  const {getProducts, process, clearError, setProcess} = useSearchProducts();
  const [productsList, setProductsList] = useState([]);
  const [filter, setFilter] = useState("all");
  const [search, setSearch] = useState("");
  const mainInfo = {thumbnail: `${mainImg}`, name: "Coffee beans. Make coffee", heading: "About our beans"};

  useEffect(() => {
    onRequest();
    // eslint-disable-next-line
  },[filter, search]);

  const onRequest = () => {
    clearError();
    getProducts()
      .then(updadeProducts)
      .then(() => setProcess('confirmed'));
  }

  const Message = () => {
    return (
      <>
        <strong className="h1 text-center">Sorry, but there is no product with such name!</strong>
      </>
    )
  }

  const updadeProducts = (items) => {
    searchProducts(items, search);
  }

  const searchProducts = (items, search) => {
    if (search.length === 0) {
      return filterProducts(items, filter);
    }

    return filterProducts(items.filter(item => item.name.toLowerCase().indexOf(search) > -1), filter);
  }
  
  const filterProducts = (items, filter) => {
    switch (filter) {
        case "africa":
            return setProductsList(items.filter(item => item.region.toLowerCase() === "africa"));
        case "south america":
            return setProductsList(items.filter(item => item.region.toLowerCase() === "south america"));
        case "asia pacific":
              return setProductsList(items.filter(item => item.region.toLowerCase() === "asia pacific"));
        case "central america":
              return setProductsList(items.filter(item => item.region.toLowerCase() === "central america"));
        default:
          return setProductsList(items);
    }
  }
  
  const onFilterSelect = (filter) => {
    setFilter(filter);
  }

  const onUpdateSearch = (search) => {
    setSearch(search);
  }

  return (
    <>
      <Helmet>
          <meta
            name="description"
            content="Page with list of different coffee"
          />
          <title>Our Coffee</title>
      </Helmet>
      <Header/>
      <div className="page-holder">
        <main className="main" id="main">
          <Intro src={bgImg}>
            <h1>Everything You Love About Coffee</h1>
          </Intro>
          <MainInfo 
            data={mainInfo}
            textCenter={true}
            text={
              <>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam finibus condimentum vehicula. Vestibulum pretium ligula risus, euismod laoreet tortor fermentum eget. Morbi tempor ipsum eget mauris lobortis efficitur. Suspendisse a est nisl. Suspendisse potenti. Sed at erat ac dolor viverra tempor. Mauris a rutrum diam. Phasellus cursus a justo eu facilisis. Maecenas iaculis sagittis mollis. Fusce auctor mi nisl, sit amet consectetur tortor mattis vel. Nunc ac enim elementum, convallis velit eget, finibus ex. In hac habitasse platea dictumst.</p>
                <p>Sed tellus tellus, laoreet eu placerat eget, pharetra non massa. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Sed sollicitudin metus eu ultrices aliquam. Morbi iaculis aliquet urna. Suspendisse potenti. Morbi sagittis eros eget volutpat porttitor. Curabitur quis pellentesque magna. Proin elementum, ante ut tempus accumsan, lectus magna imperdiet libero, non eleifend dui urna id justo. Integer sodales justo in felis aliquet, at rhoncus tellus dictum. Vivamus efficitur sapien elit, sit amet molestie leo pulvinar id. Maecenas convallis ante nibh, non vehicula enim dictum eu. Sed porttitor condimentum enim, a gravida tortor commodo eu.</p>
              </>
            }/>
          <section className="products">
            <div className="container">
                <div className="heading-block">
                  <div className="row">
                    <h1>Catalog</h1>
                    <div className="filter-block">
                      <FilterForm filter={filter} onFilterSelect={onFilterSelect} onUpdateSearch={onUpdateSearch}/>
                    </div>
                  </div>
                </div>
              {productsList.length > 0 ? setContent(process, Products, productsList) : <Message/>}
            </div>
          </section>
        </main>
      </div>
      <Footer/>
    </>
  );
}

export default OurCoffeePage;